<!-- by 添少 -->
<template>
  <section>
    <router-view></router-view>
    <section class="detail">
      <tab :select.sync="tab.select" :data="tabList" @change="handChangeTab"></tab>
      <template v-if="list.length">
        <template v-for="(item,$index) in list">
          <section class="tc1">
            <template v-if="tab.select===0||tab.select===1||tab.select===2">
              <section class="ui-thumb">
                <div class="cover">
                  <img class="db fit-cover" :src="item.preimg"/>
                </div>
                <div class="text">
                  <h4 class="l-e l1">{{item.title}}</h4>
                  <p class="info">Sasa俱乐部<b>俱乐部</b></p>
                  <p class="l-e l2">{{item.desc}}</p>
                </div>
              </section>
            </template>
            <template v-else-if="tab.select===3">
              <section class="ui-thumb">
                <div class="cover">
                  <img class="db fit-cover" :src="detail.cover"/>
                </div>
                <div class="text">
                  <h4 class="l-e l1">{{tab.select}}塞班岛{{i}}</h4>
                  <p class="info">Sasa俱乐部<b>俱乐部</b></p>
                  <p class="l-e l2">目的地简介，行程的亮点等等的摘要信息放到这里</p>
                </div>
              </section>
            </template>
          </section>
        </template>
      </template>
      <template v-else>
        <p style="text-align: center;padding: 10px;">暂无内容</p>
      </template>
    </section>
  </section>
</template>

<script>
  import { mapState } from 'vuex'
  import Tab from '@/components/tab'

  export default {
    name: 'coach',
    components: {
      Tab
    },
    data () {
      return {
        tab: {
          select: 0,
          list: [
            {value: 5, label: '课程'},
            {value: 3, label: '行程'},
            {value: 4, label: '装备'},
            {value: 2, label: '帖子'},
          ]
        },
        list: [],
        lastProductId: 0
      }
    },
    computed: {
      ...mapState({
        userInfo: state => state.userInfo
      }),
      tabList () {
        let list = []
        this.tab.list.forEach(e => list.push(e.label))
        return list
      }
    },
    created () {
      this.init()
    },
    methods: {
      init () {
        this.getList()
      },
      handChangeTab (index) {
        console.log(index)
        this.list = []
        this.lastProductId = 0
        this.getList()
      },
      getList (type) {
        if (type === 1) {
          this.$http.get(this.$port.DISCUSS_LIST_PROT, {
            params: {
              tagId: 0,
              lastDiscussId: this.lastProductId
            }
          }).then(res => {
            if (res.code === 200) {
              this.list = this.list.concat(res.data, {
                // commentNum (integer, optional): 评论数 ,
                //   cont (string, optional): 贴子内容 ,
                //   discussId (integer, optional): 贴子编号 ,
                //   headimg (string, optional): 用户头像 ,
                //   like (string, optional): 是否已点赞 见枚举值Like定义 = ['1', '2'],
                //   likeNum (integer, optional): 点赞数 ,
                //   nickname (string, optional): 用户昵称 ,
                //   preimg (string, optional): 图片(支持多张#号分隔) ,
                // publishDate (string, optional): 发布时间(格式过，直接展示) ,
                // tagId (integer, optional): 版块编号 ,
                //   tagName (string, optional): 版块名称 ,
                //   userId (integer, optional): 用户编号(如果用户类型是俱乐部则表示俱乐部编号)
              })
            }
          })
        } else {
          this.$http.get(this.$port.ARTICLELIST_PROT, {
            params: {
              contType: this.tab.list[this.tab.select].value,
              userType: this.userInfo.userType,
              lastProductId: this.lastProductId
            }
          }).then(res => {
            if (res.code === 200) {
              this.list = this.list.concat(res.data)
            }
          })
        }
      },
    }
  }
</script>

<style lang="scss" scoped>
  .ui-thumb {
    font-size: 14px;
    display: flex;
    .cover {
      height: 100px;
      flex: 0 0 130px;
    }
    .text {
      text-align: justify;
      padding-left: 10px;
      h4 {
        font-size: 18px;
      }
      .info {
        color: #888;
        b {
          background-color: #d33c44;
          font-size: 11px;
          color: #fff;
          padding: 0 5px;
          margin-left: 0.5em;
          display: inline-block;
          border-radius: 5px;
        }
      }
      flex: 1;
    }
  }
  .layout {
    background-color: #fff;
    margin-bottom: 10px;
  }
</style>



